
.event-content .event-tab {
  position: relative;
  height: 34px;
  line-height: 34px;
  font-size: 16px;
  padding-left: 20px;
  border-bottom: 1px solid #fff;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
}
.event-content .event-tab span {
    position: absolute;
    right: 20px;
    top: 8px;
    width: 10px;
    height: 10px;
    content: '';
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}
.event-content .event-tab span.close-span {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.event-content .event-box {
  list-style: none;
  overflow: hidden;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  -webkit-transition: height .3s;
  transition: height .3s;
}
.event-content .event-box ul {
    display: block;
    height: auto;
    -webkit-transition: height .3s;
    transition: height .3s;
}
.event-content .event-box ul.close-ul {
      display: none;
      height: 0px;
}
.event-content .event-box .event-list {
    position: relative;
    min-height: 44px;
    line-height: 25px;
    padding: 10px 100px 10px 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
    color: #373e40;
}
.event-content .event-box .event-list input[type=checkbox] {
      position: absolute;
      left: 15px;
      top: 12px;
      width: 20px;
      height: 20px;
}
.event-content .event-box .event-list .price-type {
      display: inline-block;
      width: 80px;
}
.event-content .event-box .event-list .price-time {
      float: right;
}
.event-content .event-box .event-list .edit {
      display: none;
}
.event-content .event-box .event-list .cancel-btn {
      position: absolute;
      right: 10px;
      top: 7px;
      width: 50px;
      height: 30px;
      line-height: 30px;
      padding: 0;
      background: #fff;
      border: 1px solid #c0ccda;
      color: #666;
      font-size: 12px;
}
.event-content .event-box .event-list .event-time {
      position: absolute;
      right: 10px;
      top: 0;
      line-height: 44px;
      font-size: 12px;
      color: #aaa;
}
.event-content .event-box .event-list .event-delete {
      text-decoration: line-through;
      color: #999;
}



