
html, body, ul, li, input {
  margin: 0;
  padding: 0;
}
body {
  font-size: 12px;
  font-family: "Helvetica Neue", Helvetica, "microsoft yahei", arial, STHeiTi, sans-serif;
  background: #fafafa;
}
input, button {
  -webkit-tap-highlight-color: transparent;
}
input[type=text] {
  -webkit-appearance: none;
}
button {
  padding: 7px 0;
  outline: none;
  text-align: center;
  border-radius: 4px;
  border: none;
  box-sizing: border-box;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
}
body, #app {
  width: 100%;
  overflow-x: hidden;
}
ul {
  list-style: none;
}
.container {
  width: 100%;
  padding: 0 10px;
  max-width: 800px;
  margin: auto;
  box-sizing: border-box;
}
.container.hide {
    display: none;
}



