
.event-add {
  position: relative;
  padding: 30px 90px 30px 0;
  font-size: 16px;
}
.event-add span {
    display: inline-block;
    padding: 5px 20px;
    margin: 0 10px 10px 0;
    border: 1px solid #c0ccda;
    border-radius: 4px;
    cursor: pointer;
    background: #fff;
    color: #999;
}
.event-add span.active {
      border: 1px solid #db4c3f;
      color: #333;
}
.event-add .n-input {
    width: 100%;
    height: 40px;
    padding: 7px 10px;
    line-height: 26px;
    border: 1px solid #c0ccda;
    border-radius: 4px;
    -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
    box-sizing: border-box;
    font-size: inherit;
}
.event-add .n-input:focus {
      outline: none;
}
.event-add .add-btn {
    position: absolute;
    right: 0;
    bottom: 30px;
    width: 80px;
    height: 40px;
    line-height: 26px;
    color: #fff;
    background: #db4c3f;
    /*transition: background .3s ease-in;*/
}
.event-add .add-btn:hover {
      background: #b54136;
}



